        :name="corte.nombre"
        :depto="corte.depto"
        :days="corte.dias_laborados"
        :m3="corte.m3_persona"
        :bonus_p="corte.bono_persona"
        :bonus_tot="corte.bono_depto"
        :asis="corte.asistencia"
        :extra="corte.$_extra_m3"
        :bonus_prod="corte.bono_productividad"
        :bonus_goals="corte.bono_metas"
        />
</div>  
</template>

<script>

import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Corte',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'corte',
            userCode: this.$route.params.user,
